<template>
  <div>
    <el-alert title="修改商品信息" type="info" center show-icon :closable='false'>
    </el-alert>
    <!-- 步骤条 -->
    <el-steps :active="stepActive" align-center>
      <el-step title="基本信息"></el-step>
      <el-step title="商品参数"></el-step>
      <el-step title="商品属性"></el-step>
      <el-step title="商品图片"></el-step>
      <el-step title="商品内容"></el-step>
      <el-step title="完成"></el-step>
    </el-steps>
    <!-- 标签页,tabs要嵌套在form里面 -->
    <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-position="top" label-width="100px">
      <!-- 表格不要指定高度，部分行不会显示 -->
      <el-tabs tab-position="left" @tab-click="tabClick" :before-leave="beforeTab">
        <el-tab-pane label="基本信息">
          <el-form-item label="商品名称" prop="goods_name">
            <el-input v-model="addCateForm.goods_name"></el-input>
          </el-form-item>
          <el-form-item label="商品价格" prop="goods_price">
            <el-input v-model="addCateForm.goods_price"></el-input>
          </el-form-item>
          <el-form-item label="商品重量" prop="goods_weight">
            <el-input v-model="addCateForm.goods_weight"></el-input>
          </el-form-item>
          <el-form-item label="商品数量" prop="goods_number">
            <el-input v-model="addCateForm.goods_number"></el-input>
          </el-form-item>
          <el-form-item label="商品分类" prop="goods_cat">
            <!-- 商品分类接口传回来的数据是数字类型的，商品信息传回来的数据是字符串的，要做转换，这里级联选择绑定的数据是number类型的 -->
            <el-cascader v-model="addCateForm.goods_cat" ref="cascaderRef" :options="catesList" placeholder="必须选择到三级分类" @change="handleChange" :props="props"
              clearable filterable>
            </el-cascader>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="商品参数">
          <el-form-item :label="item.attr_name" v-for="(item) in paramsList" :key="item.attr_id">
            <el-checkbox-group v-model="item.attr_value" @change="cheChange">
              <el-checkbox :label="cb" border v-for="(cb,i) in item.attr_vals" :key="i">
              </el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="商品属性">
          <el-form-item :label="item.attr_name" v-for="(item) in attrsList" :key="item.attr_id">
            <el-input v-model="item.attr_value" placeholder="请输入内容"></el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="商品图片">
          <!-- 上传头像组件 -->
          <el-upload :action="uploadURL" :on-preview="handlePreview" :on-remove="handleRemove" :on-success="handleSuccess" :file-list="addCateForm.pics"
            :headers="tokenHeaders" list-type="picture">
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-tab-pane>
        <el-tab-pane label="商品内容">
          <quill-editor ref="myTextEditor" v-model="addCateForm.goods_introduce" :options="editorOption"></quill-editor>
          <el-button type="primary" icon="el-icon-goods" @click="addCate">完成修改</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-form>
    <!-- 图片预览窗口 -->
    <el-dialog title="图片预览" :visible.sync="picDialogVisible" width="50%">
      <img :src="picPreview" alt="" width="100%">
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data () {
    return {
      // 当前的步骤条
      stepActive: 0,
      // 添加的商品信息表单域
      addCateForm: {
        goods_name: '',
        goods_cat: [],
        goods_price: 1,
        goods_number: 1,
        goods_weight: 1,
        goods_introduce: '',
        pics: [],
        attrs: []
      },
      // 动态参数列表
      paramsList: [],
      // 静态属性列表
      attrsList: [],
      props: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      // 上传文件的url地址
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 图片上传手动设置请求头，补充token
      tokenHeaders: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 富文本的提示信息
      editorOption: {
        placeholder: '编辑商品内容信息'
      },
      // 上传图片文件的信息
      fileList: [],
      // 商品分类信息
      catesList: [],
      // Preview图片地址
      picPreview: '',
      // 图片预览窗口
      picDialogVisible: false,
      // 表单校验
      addCateFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' },
          { min: 3, max: 25, message: '长度在 3 到 8 个字符', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请选择到三级分类', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getCatesList()
    this.getCateForm()
    console.log(this.$route.params.id)
  },
  methods: {
    // 切换tab变化
    tabClick (tab) {
      // console.log(tab)
    },
    cheChange (value) {
      console.log(this.paramsList)
    },
    // 获取所有三级分类
    async getCatesList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.catesList = res.data
    },
    // 获取商品信息
    async getCateForm () {
      const { data: res } = await this.$http.get(`goods/${this.$route.params.id}`)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      res.data.pics.forEach(item => {
        item.url = item.pics_big_url
        item.name = item.pics_id
      })
      // 重新赋值和区分下动态参数和静态属性
      res.data.attrs.forEach(item => {
        if (item.attr_sel === 'only') {
          this.attrsList.push({
            attr_name: item.attr_name,
            attr_vals: item.attr_vals,
            attr_value: item.attr_value,
            attr_id: item.attr_id
          })
        } else {
          this.paramsList.push({
            attr_name: item.attr_name,
            // vals是所有可选项，value是被选中的值，都要重新分割下
            attr_vals: item.attr_vals ? item.attr_vals.split(' ') : [],
            attr_value: item.attr_value ? item.attr_value.split(' ') : [],
            attr_id: item.attr_id
          })
        }
      })
      // 返回的goods_cat是字符串类型的，先,分割成了字符串数组，再用.map快速转成数字类型的
      res.data.goods_cat = res.data.goods_cat.split(',')
      res.data.goods_cat = res.data.goods_cat.map(Number)
      this.addCateForm = res.data
      console.log(this.addCateForm)
    },
    handleChange (value) {
      console.log(value)
      console.log(this.addCateForm)
      if (this.addCateForm.goods_cat.length !== 3) {
        this.addCateForm.goods_cat = []
        return this.$message.error('请选择三级商品分类')
      } else {
        // this.getAttrList()
      }
    },
    beforeTab (activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addCateForm.goods_cat.length !== 3) {
        this.$message.error('请先选择商品的三级分类')
        this.stepActive = 0
        return false
      }
      // stepActive需要number类型数据，字符串快速减0变成number
      this.stepActive = (activeName - 0) + 1
    },
    // 点击文件列表中已上传的文件时的钩子,用于预览时的作用
    handlePreview (file) {
      console.log(file)
      // 先判断是不是新上传的图片
      if (file.response) {
        this.picPreview = file.response.data.url
        return
      } else {
        this.picPreview = file.url
      }

      this.picDialogVisible = true
    },
    // 文件列表移除文件时的钩子
    handleRemove (file) {
      // 先判断是不是新上传的图片
      console.log(file)
      if (file.response) {
        this.addCateForm.pics.splice(this.addCateForm.pics.indexOf({ pic: file.response.data.tmp_path }), 1)
      } else {
        this.addCateForm.pics.splice(this.addCateForm.pics.indexOf(file), 1)
      }
    },
    // 图片上传完成后的狗子,使用tmp_path存储相对路径
    handleSuccess (res) {
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.addCateForm.pics.push({
        pic: res.data.tmp_path,
        url: res.data.url,
        name: res.data.tmp_path

      })
      console.log(this.addCateForm)
    },
    // 最后添加功能
    addCate () {
      this.$refs.addCateFormRef.validate(async callback => {
        if (!callback) return this.$message.error('请输入完整的商品信息！！')
        // 深克隆一份，防止奇奇怪怪的错误
        const form = _.cloneDeep(this.addCateForm)
        // 请求回来的数据已经重复了，先清空
        form.attrs = []
        this.paramsList.forEach(item => {
          form.attrs.push({
            attr_id: item.attr_id,
            // 不要写混了 value是所有被选中的值，vals是所有的可选的
            attr_value: item.attr_value.join(' '),
            attr_vals: item.attr_vals.join(' ')
          })
        })
        this.attrsList.forEach(item => {
          form.attrs.push({
            attr_id: item.attr_id,
            attr_value: item.attr_value,
            attr_vals: item.attr_vals
          })
        })
        form.goods_cat = form.goods_cat.join(',')
        console.log(form)
        const { data: res } = await this.$http.put(`goods/${this.$route.params.id}`, form)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.$router.push('/goods')
      })
    }

  }
}
</script>
<style lang="less" scoped>
.el-button {
  margin-top: 20px;
}
</style>
