<template>
  <div>
    <!-- 面包屑导航部分 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/goods' }">商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片内容部分 -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="6">
          <!-- 搜索用户功能 -->
          <!-- 优化，搜索栏小x清除搜索的数据后自动请求最新数据 -->
          <el-input v-model="pageList.query" placeholder="搜索商品名称" prefix-icon="el-icon-search" clearable @clear="getCateList">
            <!-- 搜索直接调用方法即可，query已经绑定 -->
            <el-button slot="append" icon="el-icon-search" @click="getCateList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="8">
          <el-button type="primary" icon="el-icon-goods" @click="addCate">新增商品</el-button>
        </el-col>
      </el-row>
      <!-- 用户列表展示部分 -->
      <el-table :data="cateList" stripe style="width: 100%" border>
        <el-table-column type="index" label="序号" width="50">
        </el-table-column>
        <el-table-column prop="goods_name" label="商品名称" min-width="500">
        </el-table-column>
        <el-table-column prop="goods_price" label="商品价格（￥）" width="120">
        </el-table-column>
        <el-table-column prop="goods_number" label="商品数量">
        </el-table-column>
        <el-table-column label="创建时间" width="160">
          <template slot-scope="scope">
            {{scope.row.add_time | dateFormat}}
          </template>
        </el-table-column>
        <!-- 操作用户信息部分 -->
        <el-table-column label="操作" width="180">
          <template slot-scope="scope">
            <!-- 编辑用户按钮 -->
            <el-button type="primary" icon="el-icon-edit" @click="editorDialog(scope.row.goods_id)" size="mini">编辑</el-button>
            <!-- 删除用户按钮 -->
            <el-button type="danger" icon="el-icon-delete" @click="delCate(scope.row.goods_id)" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- page分页功能部分 -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageList.pagenum" :page-sizes="[50, 30, 10, 5]"
        :page-size="pageList.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      pageList: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      cateList: []
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    async getCateList () {
      const { data: res } = await this.$http.get('goods', {
        params: this.pageList
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.total = res.data.total
      this.cateList = res.data.goods
    },
    delCate (id) {
      this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`goods/${id}`)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        // 手动修复element分页删除最后一页最后一条数据时不跳转页码的bug
        const totalPage = Math.ceil((this.total - 1) / this.pageList.pagesize) // 总页数
        this.pageList.pagenum = this.pageList.pagenum > totalPage ? totalPage : this.pageList.pagenum
        this.pageList.pagenum = this.pageList.pagenum < 1 ? 1 : this.pageList.pagenum
        this.getCateList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    editorDialog (id) {
      // 路由器传参，路由相应的也要设置解析
      this.$router.push({ name: 'editor', params: { id } })
    },
    // 显示单页显示的数量
    handleSizeChange (size) {
      this.pageList.pagesize = size
      this.getCateList()
    },
    // 翻页功能
    handleCurrentChange (current) {
      this.pageList.pagenum = current
      this.getCateList()
    },
    addCate () {
      this.$router.push('/goods/addcate')
    }
  }
}
</script>
