<template>
  <div>
    <el-alert title="新增商品信息" type="info" center show-icon :closable='false'>
    </el-alert>
    <!-- 步骤条 -->
    <el-steps :active="stepActive" align-center>
      <el-step title="基本信息"></el-step>
      <el-step title="商品参数"></el-step>
      <el-step title="商品属性"></el-step>
      <el-step title="商品图片"></el-step>
      <el-step title="商品内容"></el-step>
      <el-step title="完成"></el-step>
    </el-steps>
    <!-- 标签页,tabs要嵌套在form里面 -->
    <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-position="top" label-width="100px">
      <!-- 表格不要指定高度，部分行不会显示 -->
      <el-tabs tab-position="left" @tab-click="tabClick" :before-leave="beforeTab">
        <el-tab-pane label="基本信息">
          <el-form-item label="商品名称" prop="goods_name">
            <el-input v-model="addCateForm.goods_name"></el-input>
          </el-form-item>
          <el-form-item label="商品价格" prop="goods_price">
            <el-input v-model="addCateForm.goods_price"></el-input>
          </el-form-item>
          <el-form-item label="商品重量" prop="goods_weight">
            <el-input v-model="addCateForm.goods_weight"></el-input>
          </el-form-item>
          <el-form-item label="商品数量" prop="goods_number">
            <el-input v-model="addCateForm.goods_number"></el-input>
          </el-form-item>
          <el-form-item label="商品分类" prop="goods_cat">
            <el-cascader v-model="addCateForm.goods_cat" ref="cascaderRef" :options="catesList" placeholder="必须选择到三级分类" @change="handleChange" :props="props"
              clearable filterable>
            </el-cascader>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="商品参数">
          <el-form-item :label="item.attr_name" v-for="(item) in paramsList" :key="item.attr_id">
            <el-checkbox-group v-model="item.attr_checked_vals" @change="ckChange">
              <el-checkbox border :label="cb" v-for="(cb, i) in item.attr_vals" :key="i"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="商品属性">
          <el-form-item v-for="(item) in attrsList" :key="item.attr_id" :label="item.attr_name">
            <el-input v-model="item.attr_vals" placeholder="请输入内容"></el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="商品图片">
          <!-- 上传头像组件 -->
          <el-upload :action="uploadURL" :on-preview="handlePreview" :on-remove="handleRemove" :on-success="handleSuccess" :file-list="fileList"
            :headers="tokenHeaders" list-type="picture">
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-tab-pane>
        <el-tab-pane label="商品内容">
          <quill-editor ref="myTextEditor" v-model="addCateForm.goods_introduce" :options="editorOption"></quill-editor>
          <el-button type="primary" icon="el-icon-goods" @click="addCate">完成添加</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-form>
    <!-- 图片预览窗口 -->
    <el-dialog title="图片预览" :visible.sync="picDialogVisible" width="50%">
      <img :src="picPreview" alt="" width="100%">
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data () {
    return {
      // 当前的步骤条
      stepActive: 0,
      // 添加的商品信息表单域
      addCateForm: {
        goods_name: '',
        goods_cat: [],
        goods_price: 1,
        goods_number: 1,
        goods_weight: 1,
        goods_introduce: '',
        pics: [],
        attrs: []
      },
      // 动态参数列表
      paramsList: [],
      // 静态属性列表
      attrsList: [],
      props: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      // 上传文件的url地址
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 图片上传手动设置请求头，补充token
      tokenHeaders: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 富文本的提示信息
      editorOption: {
        placeholder: '编辑商品内容信息'
      },
      // 上传文件的信息
      fileList: [],
      // 商品信息
      catesList: [],
      // Preview图片地址
      picPreview: '',
      // 图片预览窗口
      picDialogVisible: false,
      // 表单校验
      addCateFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' },
          { min: 3, max: 25, message: '长度在 3 到 8 个字符', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请选择到三级分类', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getCatesList()
  },
  methods: {
    // 切换tab变化
    tabClick (tab) {
      // console.log(tab)
    },
    ckChange (value) {
      console.log(this.paramsList)
    },
    // 获取所有三级分类
    async getCatesList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.catesList = res.data
    },
    handleChange (value) {
      if (this.addCateForm.goods_cat.length !== 3) {
        this.addCateForm.goods_cat = []
        return this.$message.error('请选择三级商品分类')
      } else {
        this.getAttrList()
      }
    },
    // 动态获取参数属性
    async getAttrList () {
      const { data: res1 } = await this.$http.get(`categories/${this.addCateForm.goods_cat[2]}/attributes`, {
        params: {
          sel: 'only'
        }
      })

      const { data: res2 } = await this.$http.get(`categories/${this.addCateForm.goods_cat[2]}/attributes`, {
        params: {
          sel: 'many'
        }
      })
      if (res1.meta.status !== 200 && res2.meta.status !== 200) return this.$message.error(res1.meta.msg)
      res2.data.forEach(item => {
        // 空字符串切割也会返回一个不为空的字符串数组
        // 不要写错了item.attr_vals循环对象
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 使用差值表达式
        item.attr_checked_vals = [...item.attr_vals]
      })
      this.paramsList = res2.data
      console.log(this.paramsList)
      console.log(this.attrsList)
      this.attrsList = res1.data
    },
    beforeTab (activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addCateForm.goods_cat.length !== 3) {
        this.$message.error('请先选择商品的三级分类')
        this.stepActive = 0
        return false
      }
      // stepActive需要number类型数据，字符串快速减0变成number
      this.stepActive = (activeName - 0) + 1
    },
    // 点击文件列表中已上传的文件时的钩子,用于预览时的作用
    handlePreview (file) {
      this.picPreview = file.response.data.url
      this.picDialogVisible = true
    },
    // 文件列表移除文件时的钩子
    handleRemove (file) {
      this.addCateForm.pics.splice(this.addCateForm.pics.indexOf({ pic: file.response.data.tmp_path }), 1)
    },
    // 图片上传完成后的狗子,使用tmp_path存储相对路径
    handleSuccess (res) {
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.addCateForm.pics.push({ pic: res.data.tmp_path })
    },
    // 最后添加功能
    addCate () {
      this.$refs.addCateFormRef.validate(async callback => {
        if (!callback) return this.$message.error('请输入完整的商品信息！！')
        this.paramsList.forEach(item => {
          this.addCateForm.attrs.push({
            attr_id: item.attr_id,
            attr_vals: item.attr_vals,
            attr_value: item.attr_checked_vals.join(' ')
          })
        })
        this.attrsList.forEach(item => {
          this.addCateForm.attrs.push({
            attr_id: item.attr_id,
            attr_vals: item.attr_vals,
            attr_value: item.attr_vals
          })
        })
        const form = _.cloneDeep(this.addCateForm)
        form.goods_cat = form.goods_cat.join(',')
        console.log(form)
        const { data: res } = await this.$http.post('goods', form)
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.$router.push('/goods')
      })
    }

  }
}
</script>
<style lang="less" scoped>
.el-button {
  margin-top: 20px;
}
</style>
